.cut-box .cut-img{
  max-width: 100%;
  max-height: 100%;
  display: block;
}
.cut-box .cut-control-bg,
.cut-box .cut-shade{
  top: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.cut-box .cut-shade{
  background: rgba(0,0,0,0.4);
}
.cut-box .cut-view{
