.cut-box .cut-c-line{
  position: absolute;
  z-index: 2;
  display: none;
  // background: rgba(255,255,255,0.4);
}
.cut-box .cut-c-line.cut-l-left{
  width: 4px;
  height: 100%;
  left: -2px;
  cursor: w-resize;
}
.cut-box .cut-c-line.cut-l-top{
  width: 100%;
  height: 4px;
  top: -2px;
  cursor: n-resize;
}
.cut-box .cut-c-line.cut-l-right{
  width: 4px;
  height: 100%;
  right: -2px;
  cursor: w-resize;
}
.cut-box .cut-c-line.cut-l-bottom{
  width: 100%;
  height: 4px;
  bottom: -2px;
  cursor: n-resize;
}
.cut-box .cut-c-point{
  width: 9px;
  height: 9px;
  position: absolute;
  z-index: 4;
  border: solid 1px rgba(255,255,255, 0.5);
  background: rgba(0,0,0, 0.4);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.cut-box .cut-c-point.cut-p-left-top{
  left: -4px;
  top: -4px;
  cursor: nw-resize;
}
.cut-box .cut-c-point.cut-p-center-top{
  left: 50%;
  margin-left: -4px;
  margin-top: -4px;
  cursor: n-resize;
}
.cut-box .cut-c-point.cut-p-right-top{
  right: -4px;
  top: -4px;
  cursor: ne-resize;
}
.cut-box .cut-c-point.cut-p-right-middle{
  top: 50%;
  margin-top: -4px;
  right: -4px;
  cursor: w-resize;
}
.cut-box .cut-c-point.cut-p-left-middle{
  left: -4px;
  top: 50%;
  margin-top: -4px;
  cursor: w-resize;
}
.cut-box .cut-c-point.cut-p-right-bottom{
  right: -4px;
  bottom: -4px;
  cursor: nw-resize;
}
.cut-box .cut-c-point.cut-p-center-bottom{
  left: 50%;
  margin-left: -4px;
  bottom: -4px;
  cursor: n-resize;
}
.cut-box .cut-c-point.cut-p-left-bottom{
  left: -4px;
  bottom: -4px;
  cursor: ne-resize;
}